import * as React from 'react'
import { useState } from 'react'
import { Paper, Button } from '@material-ui/core'
import FilterGroup from './filter/filter-group'
import { deserialize, serialize } from './query-advanced-serialization'

const getFilterTree = (props: any) => {
  const { filters, type } = props
  return { filters, type }
}

const QueryAdvanced = (props: any) => {
  const [filterTree, setFilterTree] = useState(
    deserialize(getFilterTree(props))
  )

  return (
    <Paper
      style={{
        maxWidth: 500,
        padding: 20,
      }}
    >
      <FilterGroup
        {...filterTree}
        limitDepth={props.limitDepth}
        onChange={setFilterTree}
      />
      <Button
        style={{ marginTop: 5 }}
        fullWidth
        variant="contained"
        color="primary"
        onClick={() => props.onSearch(serialize(filterTree))}
      >
        Search
      </Button>
    </Paper>
  )
}

export default QueryAdvanced
try {
    // @ts-ignore
    queryadvanced.displayName = "queryadvanced";
    // @ts-ignore
    queryadvanced.__docgenInfo = { "description": "", "displayName": "queryadvanced", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/query-advanced/query-advanced.tsx#queryadvanced"] = { docgenInfo: queryadvanced.__docgenInfo, name: "queryadvanced", path: "src/main/webapp/query-advanced/query-advanced.tsx#queryadvanced" };
}
