import * as React from 'react'
import Select from 'react-select'
import { metacardDefinitions } from '../filter/dummyDefinitions'
import { Map } from 'immutable'
import { Box } from '@material-ui/core'

const attributeAliases = Map({
  'date-created': 'Date Created',
  enterprise: 'Enterprise',
})

const createOption = (option: string) => ({
  value: option,
  label: attributeAliases.get(option) || option,
})

const AttributeDropdown = (props: any) => {
  const options = Array.from(metacardDefinitions.keys()).map(createOption)
  const value = options.find(option => option.value === props.value)
  return (
    <Box style={{ margin: 5 }}>
      <Select
        options={options}
        value={value}
        onChange={(option: any) => {
          props.onChange(option.value)
        }}
      />
    </Box>
  )
}

export default AttributeDropdown
try {
    // @ts-ignore
    attributedropdown.displayName = "attributedropdown";
    // @ts-ignore
    attributedropdown.__docgenInfo = { "description": "", "displayName": "attributedropdown", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/query-advanced/filter/attribute-dropdown.tsx#attributedropdown"] = { docgenInfo: attributedropdown.__docgenInfo, name: "attributedropdown", path: "src/main/webapp/query-advanced/filter/attribute-dropdown.tsx#attributedropdown" };
}
